.tariffs {
    display: flex;
    flex-direction: column;
}

.containerTarifs {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media screen and (max-width: 1320px) {
    .tariffs h2 {
        font-size: 28px;
        font-weight: 900;
        line-height: 34px;
        letter-spacing: 0.01em;
        text-align: left;

        margin-bottom: 37px;

    }
}